const ADD_MESSAGE = 'ADD-MESSAGE';

const initialState = {
    messagesData: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'How are you?'},
        {id: 3, message: 'What is your name?'},
        {id: 4, message: 'Nice to meet you!'}
    ],
    dialogsData: [
        {
            id: 1,
            name: 'Sasha',
            avatar: 'data:image/jpeg;base64,'
        },
        {
            id: 2,
            name: 'Fess',
            avatar: 'data:image/jpeg;base64,'
        },
        {
            id: 3,
            name: 'Cheburashka',
            avatar: 'data:image/jpeg;base64,'
        },
        {
            id: 4,
            name: 'Leha',
            avatar: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRw0JU10zShFtHnzh74EwrB-Tzxv8GT1Peb8A&usqp=CAU'
        }
    ],
    newMessage: ''
}


export const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE: {
            return {
                ...state,
                messagesData: [...state.messagesData, {id: 5,  message: action.text}]
            };

        }
        default:   return state;
    }
}


export const addMessageCreator = (text) => ({type: ADD_MESSAGE, text})

export default dialogsReducer;