import React from 'react';
import s from './ProfileInfo.module.css'
import Preloader from '../../../comon/Preloader';
import ava from '../../../assets/images/avatar.jpg';
import ProfileStatus from "./ProfileStatus";

const ProfileInfo = (props) => {
    if (!props.profile) {
        return <Preloader/>
    }
    return (
        <div  >
            <img className={s.content}
                src='data:image/jpeg;base64,'/>
            <div className={s.descriptionBlock}>
                <img src={props.profile.photos.large != null ? props.profile.photos.large : ava}
                     className={s.avatar}/>

                <div className={s.c2row1}>Имя: {props.profile.fullName}</div>
                <div className={s.c2row2}>Обо мне: {props.profile.aboutMe}</div>
                <div className={s.c2row3}>Ищу ли работу? {props.profile.lookingForAJob ? 'Да' : 'нет'}</div>
                <div className={s.c3row1}>Facebook: {props.profile.contacts.facebook} </div>
                <div className={s.c3row2}>VK: {props.profile.contacts.vk} </div>
                <div className={s.c3row3}> Instagram: {props.profile.contacts.instagram} </div>
            </div>
            <ProfileStatus status={props.status} updateStatus={props.updateStatus}/>
        </div>
    )
}
export default ProfileInfo;