import React from "react";
import s from './Post.module.css';

const Post = (props) =>{
    return (
        <div className={s.item}>
            <div>
                <img src = 'data:image/jpeg;base64,'/>
                {props.message}
                <div>
                    <span>like {props.likeCounts}</span>
                </div>
            </div>
        </div>
    )
}
export default Post;